import * as React from "react"
import Link from "./Link"

const Pagination = ({ props }) => {
  const baseUrl = '/news/'
  const visiblePagesHalf = 2 // hom many pages before and after current should visible
  const posts = props.data.allMdx.edges
  const { currentPage, numPages } = props.pageContext
  const isFirst = currentPage === 1
  const isLast = currentPage === numPages
  const prevPage = currentPage - 1 === 1 ? baseUrl : (currentPage - 1).toString()
  const nextPage = baseUrl + (currentPage + 1).toString()
  const showEllipsisBefore = (currentPage - visiblePagesHalf) > 2
  const showEllipsisAfter = (currentPage + visiblePagesHalf) < (numPages - 1)
  const widthBefore = Math.min(visiblePagesHalf, currentPage - visiblePagesHalf)
  const widthAfter = Math.min(numPages - currentPage, visiblePagesHalf)
  const currPage = isFirst ? '' : currentPage

  return (
    <nav className="pagination mb-3" role="navigation" aria-label="navigation">
      {!isFirst && (
          <Link to={prevPage} className="pagination-previous" rel="prev">
            ← Previous Page
          </Link>
      )}
      {!isLast && (
        <Link to={nextPage} className="pagination-next" rel="prev">
          Next Page →
        </Link>
      )}
      <ul className="pagination-list">
      {(currentPage - visiblePagesHalf) >= 0 && (
        <li><Link to={baseUrl} className="pagination-link" aria-label="Goto page 1">1</Link></li>
      )}
      {showEllipsisBefore && (
        <li><span className="pagination-ellipsis">&hellip;</span></li>
      )}
      {(currentPage - visiblePagesHalf) > 0 && (
        Array(widthBefore).fill(1).map((el, i) =>
          <li><Link to={`${baseUrl}${currentPage - widthBefore + i}`} className="pagination-link" aria-label={`Page ${currentPage - widthBefore + i}`} aria-current="page">{currentPage - widthBefore + i}</Link></li>
        )
      )}
      <li><Link to={`${baseUrl}${currPage}`} className="pagination-link is-current" aria-label={`Page ${currentPage}`} aria-current="page">{currentPage}</Link></li>
      {Array(Math.min(numPages - currentPage, visiblePagesHalf)).fill(1).map((el, i) =>
        <li><Link to={`${baseUrl}${currentPage + i + 1}`} className="pagination-link" aria-label={`Page ${currentPage + i + 1}`}>{currentPage + i + 1}</Link></li>
      )}
      {showEllipsisAfter && (
        <li><span className="pagination-ellipsis">&hellip;</span></li>
      )}
      {((currentPage + visiblePagesHalf) < numPages) && (
        <li><Link to={`${baseUrl}${numPages}`} className="pagination-link" aria-label={`Page ${numPages}`}>{numPages}</Link></li>
      )}
      </ul>
    </nav>
  )
}

export default Pagination
