import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layouts/layout"
import Link from "../components/Link"
import Pagination from "../components/pagination"
import { GatsbyImage, getImage } from "gatsby-plugin-image"


export default class BlogList extends React.Component {
  render() {
    const posts = this.props.data.allMdx.edges

    return (
      <Layout title={"News"}>

      <div className="container">
          <section className="articles">
              <div className="column is-8 is-offset-2">

        {posts.map(({ node }) => {
          const title = node.frontmatter.title || node.frontmatter.slug
          const artDate = node.frontmatter.date
          let image = null
          let alt = null
          if (node.frontmatter.images) {
            image = node.frontmatter.images[0]
            alt = image.caption | image.name | title
          }

          return (
            <div key={node.frontmatter.slug} className="box">
              <div className="box-header">
                <h4 className="title is-4">
                  <Link to={node.frontmatter.slug}>{title}
                  </Link>
                  <span className="labels is-pulled-right">
                    <a href="label.html"> </a>
                  </span>
                </h4>
                <small>{artDate}</small>
              </div>
              <div className="box-content">
                <div className="columns">
                  {(image) && (
                    <div className="column is-2">
                      <GatsbyImage
                        image={getImage(image.url)}
                        alt={alt}
                      />
                    </div>
                  )}
                  <div className="column">
                    <div className="box-content-text">
                      <p>{node.excerpt}</p>
                    </div>
                    <div className="has-text-right">
                      <Link to={node.frontmatter.slug} className="button is-small">Lesen</Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )
        })}

            </div>

            <Pagination props={this.props} />
          </section>
        </div>

      </Layout>
    )
  }
}

export const blogListQuery = graphql`
  query blogListQuery($skip: Int!, $limit: Int!) {
    allMdx(
      filter: {frontmatter: {slug: {regex: "/news/"}}}
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          excerpt(pruneLength: 350)
          frontmatter {
            date(formatString: "YYYY-MM-DD")
            slug
            title
            images {
              caption
              name
              url {
                childImageSharp {
                  gatsbyImageData(
                    layout: CONSTRAINED
                    width: 256
                    height: 256
                    placeholder: BLURRED
                    transformOptions: {fit:COVER, cropFocus:ATTENTION}
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
              }
            }
          }
        }
      }
    }
  }
`
